import { Navigate, useLocation } from 'react-router-dom'
import { authService, adminService } from '../../api'

const ProtectedRoute = ({ children, requireAuth = true, requireAdmin = false }) => {
  const location = useLocation()
  const isAuthenticated = authService.isAuthenticated()
  const isAdmin = adminService.isAdmin()

  // If authentication is required but user is not authenticated
  if (requireAuth && !isAuthenticated) {
    return <Navigate to="/login" state={{ from: location }} replace />
  }

  // If admin access is required but user is not admin
  if (requireAdmin && !isAdmin) {
    return <Navigate to="/admin/login" state={{ from: location }} replace />
  }

  // If user is authenticated but trying to access login/register pages
  if (!requireAuth && isAuthenticated && !requireAdmin && !isAdmin) {
    return <Navigate to="/dashboard" replace />
  }

  // If admin is authenticated but trying to access admin login
  if (!requireAuth && isAdmin && requireAdmin) {
    return <Navigate to="/admin/dashboard" replace />
  }

  return children
}

export default ProtectedRoute
