import { useState, useEffect } from 'react'
import { Link, useNavigate } from 'react-router-dom'
import { authService } from '../../api'
import addressService from '../../api/Model/addressService'
import AutocompleteInput from '../Common/AutocompleteInput'



const Register = () => {
  const [formData, setFormData] = useState({
    firstName: '',
    lastName: '',
    email: '',
    password: '',
    confirmPassword: '',
    mobileNumber: '',
    address: {
      street: '',
      city: '',
      state: '',
      country: '',
      zipCode: ''
    }
  })
  const [errors, setErrors] = useState({})
  const [isLoading, setIsLoading] = useState(false)
  const [isEmailSent, setIsEmailSent] = useState(false)
  const [isValidatingAddress, setIsValidatingAddress] = useState(false)
  const [addressValidation, setAddressValidation] = useState(null)
  
  // Address suggestions state
  const [streetSuggestions, setStreetSuggestions] = useState([])
  const [citySuggestions, setCitySuggestions] = useState([])
  const [stateSuggestions, setStateSuggestions] = useState([])
  const [countrySuggestions, setCountrySuggestions] = useState([])
  const [isLoadingStreet, setIsLoadingStreet] = useState(false)
  const [isLoadingCity, setIsLoadingCity] = useState(false)
  const [isLoadingState, setIsLoadingState] = useState(false)
  const [isLoadingCountry, setIsLoadingCountry] = useState(false)
  
  const navigate = useNavigate()



  // International phone number validation
  const validatePhoneNumber = (phoneNumber) => {
    // Remove all non-digit characters except + and -
    const cleaned = phoneNumber.replace(/[^\d+\-()\s]/g, '')
    
    // Basic international format validation
    const phoneRegex = /^(\+?1-?)?(\([0-9]{3}\)|[0-9]{3})-?[0-9]{3}-?[0-9]{4}$/
    const internationalRegex = /^\+[1-9]\d{1,14}$/
    
    return phoneRegex.test(cleaned) || internationalRegex.test(cleaned)
  }

  // Real-time address validation with geolocation
  const validateAddress = async (address) => {
    return await addressService.validateAddress(address)
  }

  // Get address suggestions
  const getAddressSuggestions = async (query, type) => {
    if (!query || query.length < 2) return
    
    switch (type) {
      case 'street':
        setIsLoadingStreet(true)
        const streetResults = await addressService.getAddressSuggestions(query)
        setStreetSuggestions(streetResults)
        setIsLoadingStreet(false)
        break
      case 'city':
        setIsLoadingCity(true)
        const cityResults = await addressService.getCitySuggestions(query)
        setCitySuggestions(cityResults)
        setIsLoadingCity(false)
        break
      case 'state':
        setIsLoadingState(true)
        const stateResults = await addressService.getStateSuggestions(query)
        setStateSuggestions(stateResults)
        setIsLoadingState(false)
        break
      case 'country':
        setIsLoadingCountry(true)
        const countryResults = await addressService.getCountrySuggestions(query)
        setCountrySuggestions(countryResults)
        setIsLoadingCountry(false)
        break
    }
  }

  const handleChange = async (e) => {
    const { name, value } = e.target
    
    if (name.includes('.')) {
      // Handle nested address fields
      const [parent, child] = name.split('.')
      setFormData(prev => ({
        ...prev,
        [parent]: {
          ...prev[parent],
          [child]: value
        }
      }))
      
      // Real-time address validation and suggestions
      if (parent === 'address') {
        const newAddress = {
          ...formData.address,
          [child]: value
        }
        
        // Get suggestions for the current field
        if (child === 'street' && value.length >= 3) {
          // Use street-specific suggestions for better results
          const streetResults = await addressService.getStreetSuggestions(value)
          setStreetSuggestions(streetResults)
        } else if (child === 'city' && value.length >= 2) {
          const cityResults = await addressService.getCitySuggestions(value)
          setCitySuggestions(cityResults)
        } else if (child === 'state' && value.length >= 2) {
          const stateResults = await addressService.getStateSuggestions(value)
          setStateSuggestions(stateResults)
        } else if (child === 'country' && value.length >= 2) {
          const countryResults = await addressService.getCountrySuggestions(value)
          setCountrySuggestions(countryResults)
        }
        
        // Clear previous timeout
        if (window.addressValidationTimeout) {
          clearTimeout(window.addressValidationTimeout)
        }
        
        // Debounce the validation to avoid too many API calls
        window.addressValidationTimeout = setTimeout(async () => {
          setIsValidatingAddress(true)
          const validation = await validateAddress(newAddress)
          setAddressValidation(validation)
          setIsValidatingAddress(false)
        }, 1000) // Wait 1 second after user stops typing
      }
    } else {
      setFormData(prev => ({
        ...prev,
        [name]: value
      }))
    }
    
    // Clear error when user starts typing
    if (errors[name]) {
      setErrors(prev => ({
        ...prev,
        [name]: ''
      }))
    }
  }

  // Handle address field selection from autocomplete
  const handleAddressFieldSelect = (suggestion, field) => {
    if (field === 'street') {
      setFormData(prev => ({
        ...prev,
        address: {
          ...prev.address,
          street: suggestion.street || suggestion.display,
          city: suggestion.city || prev.address.city,
          state: suggestion.state || prev.address.state,
          country: suggestion.country || prev.address.country,
          zipCode: suggestion.postcode || prev.address.zipCode
        }
      }))
    } else if (field === 'city') {
      setFormData(prev => ({
        ...prev,
        address: {
          ...prev.address,
          city: suggestion.name,
          state: suggestion.state || prev.address.state,
          country: suggestion.country || prev.address.country
        }
      }))
    } else if (field === 'state') {
      setFormData(prev => ({
        ...prev,
        address: {
          ...prev.address,
          state: suggestion.name,
          country: suggestion.country || prev.address.country
        }
      }))
    } else if (field === 'country') {
      setFormData(prev => ({
        ...prev,
        address: {
          ...prev.address,
          country: suggestion.name
        }
      }))
    }
    
    // Clear suggestions
    setStreetSuggestions([])
    setCitySuggestions([])
    setStateSuggestions([])
    setCountrySuggestions([])
  }

  const validateForm = () => {
    const newErrors = {}
    
    // First name validation - 2-50 characters per Auth.md
    if (!formData.firstName.trim()) {
      newErrors.firstName = 'First name is required'
    } else if (formData.firstName.trim().length < 2 || formData.firstName.trim().length > 50) {
      newErrors.firstName = 'First name must be between 2 and 50 characters'
    }
    
    // Last name validation - 2-50 characters per Auth.md
    if (!formData.lastName.trim()) {
      newErrors.lastName = 'Last name is required'
    } else if (formData.lastName.trim().length < 2 || formData.lastName.trim().length > 50) {
      newErrors.lastName = 'Last name must be between 2 and 50 characters'
    }
    
    // Email validation per Auth.md
    if (!formData.email) {
      newErrors.email = 'Email is required'
    } else if (!/\S+@\S+\.\S+/.test(formData.email)) {
      newErrors.email = 'Please enter a valid email'
    }
    
    // Password validation - minimum 6 characters per Auth.md
    if (!formData.password) {
      newErrors.password = 'Password is required'
    } else if (formData.password.length < 6) {
      newErrors.password = 'Password must be at least 6 characters'
    }
    
    // Confirm password validation per Auth.md
    if (!formData.confirmPassword) {
      newErrors.confirmPassword = 'Please confirm your password'
    } else if (formData.password !== formData.confirmPassword) {
      newErrors.confirmPassword = 'Passwords do not match'
    }

    // Mobile number validation (optional field not in Auth.md)
    if (formData.mobileNumber && !validatePhoneNumber(formData.mobileNumber)) {
      newErrors.mobileNumber = 'Please enter a valid international mobile number (e.g., +1-555-123-4567 or +44 20 7946 0958)'
    }

    // Address validation - if any address field is filled, all are required
    const hasAnyAddressField = formData.address.street || formData.address.city || 
                              formData.address.state || formData.address.country || 
                              formData.address.zipCode
    
    if (hasAnyAddressField) {
      if (!formData.address.street.trim()) {
        newErrors['address.street'] = 'Street address is required'
      }
      if (!formData.address.city.trim()) {
        newErrors['address.city'] = 'City is required'
      }
      if (!formData.address.state.trim()) {
        newErrors['address.state'] = 'State is required'
      }
      if (!formData.address.country.trim()) {
        newErrors['address.country'] = 'Country is required'
      }
      if (!formData.address.zipCode.trim()) {
        newErrors['address.zipCode'] = 'ZIP code is required'
      }
    }

    setErrors(newErrors)
    return Object.keys(newErrors).length === 0
  }

  const handleSubmit = async (e) => {
    e.preventDefault()
    
    if (!validateForm()) return

    setIsLoading(true)
    
    try {
      // Only send required fields per Auth.md specification
      const registrationData = {
        firstName: formData.firstName,
        lastName: formData.lastName,
        email: formData.email,
        password: formData.password,
        confirmPassword: formData.confirmPassword
      }

      // Add optional fields only if they have values
      if (formData.mobileNumber && formData.mobileNumber.trim()) {
        registrationData.mobileNumber = formData.mobileNumber.trim()
      }
      
      if (formData.address.street && formData.address.street.trim()) {
        registrationData.address = {
          street: formData.address.street.trim(),
          city: formData.address.city.trim(),
          state: formData.address.state.trim(),
          country: formData.address.country.trim(),
          zipCode: formData.address.zipCode.trim()
        }
      }

      const response = await authService.register(registrationData)

      setIsEmailSent(true)
      console.log('Registration successful:', response.message)
      
    } catch (error) {
      console.error('Registration error:', error)
      setErrors({ 
        general: error.message || 'Registration failed. Please try again.' 
      })
    } finally {
      setIsLoading(false)
    }
  }

  if (isEmailSent) {
    return (
      <>
        {/* Main Page Header for Register */}
        <header className="relative z-10 bg-white/95 backdrop-blur-sm border-b border-gray-100 sticky top-0">
          <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
            <div className="flex justify-between items-center py-4">
              <div className="flex items-center space-x-3">
                <Link to="/" className="flex items-center space-x-3">
                  <div className="w-8 h-8 bg-orange-500 rounded-lg flex items-center justify-center border border-orange-300">
                    <span className="text-white font-bold text-sm">R</span>
                  </div>
                  <h1 className="text-xl font-bold text-gray-900">
                    ResumeCraft
                  </h1>
                </Link>
              </div>
              <nav className="hidden md:flex space-x-8">
                <Link to="/" className="text-gray-600 hover:text-orange-500 font-medium transition-colors text-sm">Home</Link>
                <Link to="/login" className="text-gray-600 hover:text-orange-500 font-medium transition-colors text-sm">Sign In</Link>
              </nav>
              <div className="flex items-center space-x-4">
                <Link to="/login" className="btn-primary text-sm px-4 py-2">Sign In</Link>
              </div>
            </div>
          </div>
        </header>
        
        <div className="min-h-screen bg-gray-50 flex flex-col justify-center py-12 sm:px-6 lg:px-8">
          <div className="sm:mx-auto sm:w-full sm:max-w-md">
            <div className="flex justify-center">
              <div className="w-8 h-8 bg-orange-500 rounded-lg flex items-center justify-center">
                <span className="text-white font-bold text-sm">R</span>
              </div>
            </div>
            <h2 className="mt-6 text-center text-3xl font-bold text-gray-900">
              Check your email
            </h2>
            <p className="mt-2 text-center text-sm text-gray-600">
              We've sent a verification link to {formData.email}
            </p>
          </div>

          <div className="mt-8 sm:mx-auto sm:w-full sm:max-w-md">
            <div className="bg-white py-8 px-4 shadow sm:rounded-lg sm:px-10 text-center">
              <div className="text-orange-500 mb-4">
                <svg className="mx-auto h-12 w-12" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 8l7.89 4.26a2 2 0 002.22 0L21 8M5 19h14a2 2 0 002-2V7a2 2 0 00-2-2H5a2 2 0 00-2 2v10a2 2 0 002 2z" />
                </svg>
              </div>
              <h3 className="text-lg font-medium text-gray-900 mb-2">
                Verify your email address
              </h3>
              <p className="text-sm text-gray-600 mb-6">
                Click the link in the email we sent to verify your account and start building your resume.
              </p>
              <button
                onClick={() => setIsEmailSent(false)}
                className="text-orange-500 hover:text-orange-600 font-medium"
              >
                Didn't receive the email? Try again
              </button>
            </div>
          </div>
        </div>
      </>
    )
  }

  return (
    <>
      {/* Main Page Header for Register */}
      <header className="relative z-10 bg-white/95 backdrop-blur-sm border-b border-gray-100 sticky top-0">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between items-center py-4">
            <div className="flex items-center space-x-3">
              <Link to="/" className="flex items-center space-x-3">
                <div className="w-8 h-8 bg-orange-500 rounded-lg flex items-center justify-center border border-orange-300">
                  <span className="text-white font-bold text-sm">R</span>
                </div>
                <h1 className="text-xl font-bold text-gray-900">
                  ResumeCraft
                </h1>
              </Link>
            </div>
            <nav className="hidden md:flex space-x-8">
              <Link to="/" className="text-gray-600 hover:text-orange-500 font-medium transition-colors text-sm">Home</Link>
              <Link to="/login" className="text-gray-600 hover:text-orange-500 font-medium transition-colors text-sm">Sign In</Link>
            </nav>
            <div className="flex items-center space-x-4">
              <Link to="/login" className="btn-primary text-sm px-4 py-2">Sign In</Link>
            </div>
          </div>
        </div>
      </header>
      
      <div className="min-h-screen bg-gray-50 flex flex-col justify-center py-12 sm:px-6 lg:px-8">
        <div className="sm:mx-auto sm:w-full sm:max-w-md">
          <div className="flex justify-center">
            <div className="w-8 h-8 bg-orange-500 rounded-lg flex items-center justify-center">
              <span className="text-white font-bold text-sm">R</span>
            </div>
          </div>
          <h2 className="mt-6 text-center text-3xl font-bold text-gray-900">
            Create your account
          </h2>
          <p className="mt-2 text-center text-sm text-gray-600">
            Or{' '}
            <Link to="/login" className="font-medium text-orange-500 hover:text-orange-600">
              sign in to your existing account
            </Link>
          </p>
        </div>

        <div className="mt-8 sm:mx-auto sm:w-full sm:max-w-md">
          <div className="bg-white py-8 px-4 shadow sm:rounded-lg sm:px-10">
            <form className="space-y-6" onSubmit={handleSubmit}>
              {errors.general && (
                <div className="bg-red-50 border border-red-200 text-red-600 px-4 py-3 rounded-md text-sm">
                  {errors.general}
                </div>
              )}

              {/* Name Fields */}
              <div className="grid grid-cols-1 gap-6 sm:grid-cols-2">
                <div>
                  <label htmlFor="firstName" className="block text-sm font-medium text-gray-700">
                    First name
                  </label>
                  <div className="mt-1">
                    <input
                      id="firstName"
                      name="firstName"
                      type="text"
                      autoComplete="given-name"
                      required
                      value={formData.firstName}
                      onChange={handleChange}
                      className={`appearance-none block w-full px-3 py-2 border rounded-md shadow-sm placeholder-gray-400 focus:outline-none focus:ring-orange-500 focus:border-orange-500 sm:text-sm ${
                        errors.firstName ? 'border-red-300' : 'border-gray-300'
                      }`}
                    />
                    {errors.firstName && (
                      <p className="mt-2 text-sm text-red-600">{errors.firstName}</p>
                    )}
                  </div>
                </div>

                <div>
                  <label htmlFor="lastName" className="block text-sm font-medium text-gray-700">
                    Last name
                  </label>
                  <div className="mt-1">
                    <input
                      id="lastName"
                      name="lastName"
                      type="text"
                      autoComplete="family-name"
                      required
                      value={formData.lastName}
                      onChange={handleChange}
                      className={`appearance-none block w-full px-3 py-2 border rounded-md shadow-sm placeholder-gray-400 focus:outline-none focus:ring-orange-500 focus:border-orange-500 sm:text-sm ${
                        errors.lastName ? 'border-red-300' : 'border-gray-300'
                      }`}
                    />
                    {errors.lastName && (
                      <p className="mt-2 text-sm text-red-600">{errors.lastName}</p>
                    )}
                  </div>
                </div>
              </div>

              {/* Email */}
              <div>
                <label htmlFor="email" className="block text-sm font-medium text-gray-700">
                  Email address
                </label>
                <div className="mt-1">
                  <input
                    id="email"
                    name="email"
                    type="email"
                    autoComplete="email"
                    required
                    value={formData.email}
                    onChange={handleChange}
                    className={`appearance-none block w-full px-3 py-2 border rounded-md shadow-sm placeholder-gray-400 focus:outline-none focus:ring-orange-500 focus:border-orange-500 sm:text-sm ${
                      errors.email ? 'border-red-300' : 'border-gray-300'
                    }`}
                  />
                  {errors.email && (
                    <p className="mt-2 text-sm text-red-600">{errors.email}</p>
                  )}
                </div>
              </div>

              {/* Mobile Number */}
              <div>
                <label htmlFor="mobileNumber" className="block text-sm font-medium text-gray-700">
                  Mobile Number (International) - Optional
                </label>
                <div className="mt-1">
                  <input
                    id="mobileNumber"
                    name="mobileNumber"
                    type="tel"
                    autoComplete="tel"
                    required
                    placeholder="+1-555-123-4567 or +44 20 7946 0958"
                    value={formData.mobileNumber}
                    onChange={handleChange}
                    className={`appearance-none block w-full px-3 py-2 border rounded-md shadow-sm placeholder-gray-400 focus:outline-none focus:ring-orange-500 focus:border-orange-500 sm:text-sm ${
                      errors.mobileNumber ? 'border-red-300' : 'border-gray-300'
                    }`}
                  />
                  {errors.mobileNumber && (
                    <p className="mt-2 text-sm text-red-600">{errors.mobileNumber}</p>
                  )}
                  <p className="mt-1 text-xs text-gray-500">
                    Enter your mobile number in international format
                  </p>
                </div>
              </div>

              {/* Address Section */}
              <div className="border-t border-gray-200 pt-6">
                <h3 className="text-lg font-medium text-gray-900 mb-4">Address Information (Optional)</h3>
                
                <div className="space-y-4">
                                    <div>
                    <label htmlFor="address.street" className="block text-sm font-medium text-gray-700">
                      Street Address
                    </label>
                    <div className="mt-1">
                      <AutocompleteInput
                        value={formData.address.street}
                        onChange={handleChange}
                        onSelect={(suggestion) => handleAddressFieldSelect(suggestion, 'street')}
                        placeholder="Start typing street address..."
                        suggestions={streetSuggestions}
                        isLoading={isLoadingStreet}
                        name="address.street"
                        className={errors['address.street'] ? 'border-red-300' : 'border-gray-300'}
                      />
                      {errors['address.street'] && (
                        <p className="mt-2 text-sm text-red-600">{errors['address.street']}</p>
                      )}
                    </div>
                  </div>

                  <div className="grid grid-cols-1 gap-4 sm:grid-cols-2">
                    <div>
                      <label htmlFor="address.city" className="block text-sm font-medium text-gray-700">
                        City
                      </label>
                      <div className="mt-1">
                        <AutocompleteInput
                          value={formData.address.city}
                          onChange={handleChange}
                          onSelect={(suggestion) => handleAddressFieldSelect(suggestion, 'city')}
                          placeholder="Start typing city name..."
                          suggestions={citySuggestions}
                          isLoading={isLoadingCity}
                          name="address.city"
                          className={errors['address.city'] ? 'border-red-300' : 'border-gray-300'}
                        />
                        {errors['address.city'] && (
                          <p className="mt-2 text-sm text-red-600">{errors['address.city']}</p>
                        )}
                      </div>
                    </div>

                    <div>
                      <label htmlFor="address.state" className="block text-sm font-medium text-gray-700">
                        State/Province
                      </label>
                      <div className="mt-1">
                        <AutocompleteInput
                          value={formData.address.state}
                          onChange={handleChange}
                          onSelect={(suggestion) => handleAddressFieldSelect(suggestion, 'state')}
                          placeholder="Start typing state/province..."
                          suggestions={stateSuggestions}
                          isLoading={isLoadingState}
                          name="address.state"
                          className={errors['address.state'] ? 'border-red-300' : 'border-gray-300'}
                        />
                        {errors['address.state'] && (
                          <p className="mt-2 text-sm text-red-600">{errors['address.state']}</p>
                        )}
                      </div>
                    </div>
                  </div>

                  <div className="grid grid-cols-1 gap-4 sm:grid-cols-2">
                    <div>
                      <label htmlFor="address.country" className="block text-sm font-medium text-gray-700">
                        Country
                      </label>
                      <div className="mt-1">
                        <AutocompleteInput
                          value={formData.address.country}
                          onChange={handleChange}
                          onSelect={(suggestion) => handleAddressFieldSelect(suggestion, 'country')}
                          placeholder="Start typing country name..."
                          suggestions={countrySuggestions}
                          isLoading={isLoadingCountry}
                          name="address.country"
                          className={errors['address.country'] ? 'border-red-300' : 'border-gray-300'}
                        />
                        {errors['address.country'] && (
                          <p className="mt-2 text-sm text-red-600">{errors['address.country']}</p>
                        )}
                      </div>
                    </div>

                    <div>
                      <label htmlFor="address.zipCode" className="block text-sm font-medium text-gray-700">
                        ZIP/Postal Code
                      </label>
                      <div className="mt-1">
                        <input
                          id="address.zipCode"
                          name="address.zipCode"
                          type="text"
                          autoComplete="postal-code"
                          required
                          value={formData.address.zipCode}
                          onChange={handleChange}
                          className={`appearance-none block w-full px-3 py-2 border rounded-md shadow-sm placeholder-gray-400 focus:outline-none focus:ring-orange-500 focus:border-orange-500 sm:text-sm ${
                            errors['address.zipCode'] ? 'border-red-300' : 'border-gray-300'
                          }`}
                        />
                        {errors['address.zipCode'] && (
                          <p className="mt-2 text-sm text-red-600">{errors['address.zipCode']}</p>
                        )}
                      </div>
                    </div>
                  </div>

                  {/* Real-time Address Validation */}
                  <div className="space-y-2">
                    <div className="flex items-center justify-end">
                      {isValidatingAddress && (
                        <div className="flex items-center space-x-2">
                          <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-orange-500"></div>
                          <span className="text-sm text-gray-600">Validating address...</span>
                        </div>
                      )}
                      {addressValidation && !isValidatingAddress && (
                        <span className={`text-sm ${addressValidation.isValid ? 'text-green-600' : 'text-red-600'}`}>
                          {addressValidation.message}
                        </span>
                      )}
                    </div>
                    

                  </div>
                </div>
              </div>

              {/* Password Fields */}
              <div className="border-t border-gray-200 pt-6">
                <div>
                  <label htmlFor="password" className="block text-sm font-medium text-gray-700">
                    Password
                  </label>
                  <div className="mt-1">
                    <input
                      id="password"
                      name="password"
                      type="password"
                      autoComplete="new-password"
                      required
                      value={formData.password}
                      onChange={handleChange}
                      className={`appearance-none block w-full px-3 py-2 border rounded-md shadow-sm placeholder-gray-400 focus:outline-none focus:ring-orange-500 focus:border-orange-500 sm:text-sm ${
                        errors.password ? 'border-red-300' : 'border-gray-300'
                      }`}
                    />
                    {errors.password && (
                      <p className="mt-2 text-sm text-red-600">{errors.password}</p>
                    )}
                  </div>
                </div>

                <div className="mt-4">
                  <label htmlFor="confirmPassword" className="block text-sm font-medium text-gray-700">
                    Confirm password
                  </label>
                  <div className="mt-1">
                    <input
                      id="confirmPassword"
                      name="confirmPassword"
                      type="password"
                      autoComplete="new-password"
                      required
                      value={formData.confirmPassword}
                      onChange={handleChange}
                      className={`appearance-none block w-full px-3 py-2 border rounded-md shadow-sm placeholder-gray-400 focus:outline-none focus:ring-orange-500 focus:border-orange-500 sm:text-sm ${
                        errors.confirmPassword ? 'border-red-300' : 'border-gray-300'
                      }`}
                    />
                    {errors.confirmPassword && (
                      <p className="mt-2 text-sm text-red-600">{errors.confirmPassword}</p>
                    )}
                  </div>
                </div>
              </div>

              <div className="flex items-center">
                <input
                  id="agree-terms"
                  name="agree-terms"
                  type="checkbox"
                  required
                  className="h-4 w-4 text-orange-500 focus:ring-orange-500 border-gray-300 rounded"
                />
                <label htmlFor="agree-terms" className="ml-2 block text-sm text-gray-900">
                  I agree to the{' '}
                  <Link to="/terms" className="text-orange-500 hover:text-orange-600">
                    Terms of Service
                  </Link>{' '}
                  and{' '}
                  <Link to="/privacy" className="text-orange-500 hover:text-orange-600">
                    Privacy Policy
                  </Link>
                </label>
              </div>

              <div>
                <button
                  type="submit"
                  disabled={isLoading}
                  className="w-full flex justify-center py-2 px-4 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-orange-500 hover:bg-orange-600 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-orange-500 disabled:opacity-50 disabled:cursor-not-allowed"
                >
                  {isLoading ? 'Creating account...' : 'Create account'}
                </button>
              </div>
            </form>

            <div className="mt-6">
              <div className="relative">
                <div className="absolute inset-0 flex items-center">
                  <div className="w-full border-t border-gray-300" />
                </div>
                <div className="relative flex justify-center text-sm">
                  <span className="px-2 bg-white text-gray-500">Or continue with</span>
                </div>
              </div>

              <div className="mt-6 grid grid-cols-2 gap-3">
                <button className="w-full inline-flex justify-center py-2 px-4 border border-gray-300 rounded-md shadow-sm bg-white text-sm font-medium text-gray-500 hover:bg-gray-50">
                  <svg className="w-5 h-5" viewBox="0 0 24 24">
                    <path fill="currentColor" d="M22.56 12.25c0-.78-.07-1.53-.2-2.25H12v4.26h5.92c-.26 1.37-1.04 2.53-2.21 3.31v2.77h3.57c2.08-1.92 3.28-4.74 3.28-8.09z"/>
                    <path fill="currentColor" d="M12 23c2.97 0 5.46-.98 7.28-2.66l-3.57-2.77c-.98.66-2.23 1.06-3.71 1.06-2.86 0-5.29-1.93-6.16-4.53H2.18v2.84C3.99 20.53 7.7 23 12 23z"/>
                    <path fill="currentColor" d="M5.84 14.09c-.22-.66-.35-1.36-.35-2.09s.13-1.43.35-2.09V7.07H2.18C1.43 8.55 1 10.22 1 12s.43 3.45 1.18 4.93l2.85-2.22.81-.62z"/>
                    <path fill="currentColor" d="M12 5.38c1.62 0 3.06.56 4.21 1.64l3.15-3.15C17.45 2.09 14.97 1 12 1 7.7 1 3.99 3.47 2.18 7.07l3.66 2.84c.87-2.6 3.3-4.53 6.16-4.53z"/>
                  </svg>
                  <span className="ml-2">Google</span>
                </button>

                <button className="w-full inline-flex justify-center py-2 px-4 border border-gray-300 rounded-md shadow-sm bg-white text-sm font-medium text-gray-500 hover:bg-gray-50">
                  <svg className="w-5 h-5" fill="currentColor" viewBox="0 0 24 24">
                    <path d="M24 4.557c-.883.392-1.832.656-2.828.775 1.017-.609 1.798-1.574 2.165-2.724-.951.564-2.005.974-3.127 1.195-.897-.957-2.178-1.555-3.594-1.555-3.179 0-5.515 2.966-4.797 6.045-4.091-.205-7.719-2.165-10.148-5.144-1.29 2.213-.669 5.108 1.523 6.574-.806-.026-1.566-.247-2.229-.616-.054 2.281 1.581 4.415 3.949 4.89-.693.188-1.452.232-2.224.084.626 1.956 2.444 3.379 4.6 3.419-2.07 1.623-4.678 2.348-7.29 2.04 2.179 1.397 4.768 2.212 7.548 2.212 9.142 0 14.307-7.721 13.995-14.646.962-.695 1.797-1.562 2.457-2.549z"/>
                  </svg>
                  <span className="ml-2">Twitter</span>
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  )
}

export default Register
